<template>
    <div class="dynamic-type-list">
        <navigate title="选择发布类别" position="absolute"/>
        <div class="scroll-wrapper" style="padding-top: 46px">
            <mu-list class="list">
                <mu-list-item class="list-item border-1px-b" :to="{name: 'dynamic-issue'}" button>
                    <mu-list-item-title>动态圈</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" :to="{name: 'dynamic-supply-info'}" button>
                    <mu-list-item-title>货源信息</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" :to="{name: 'dynamic-transport-info'}" button>
                    <mu-list-item-title>船舶待运</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" :to="{name: 'dynamic-job-info'}" button>
                    <mu-list-item-title>个人求职</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="list-item border-1px-b" :to="{name: 'dynamic-recruit-info'}" button>
                    <mu-list-item-title>企业招聘</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right" color="rgba(0,0,0,.12)"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>

        <transition name="views-left">
            <keep-alive>
                <router-view class="views" @refresh="$emit('refresh')"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"

    const name = 'DYNAMIC_TYPE_LIST'

    export default {
        name,
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .dynamic-type-list
        > .scroll-wrapper
            width 100%
            height 100%
            background-color #EFEFEF
            .list
                padding 0
                background-color #fff

    #type-views
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 1000
        background-color #fff
</style>



